@keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}
@-webkit-keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}
@-moz-keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}
@-ms-keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}

.stars,
.twinkling {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: block;
}

.stars {
  background: #000
    url('../../images/animations/stars-2.svg') repeat top
    center;
  z-index: 0;
}

.twinkling {
  background: transparent url("../../images/animations/twinkling-2.svg") repeat top center;
  z-index: 0;

  -moz-animation: move-twink-back 75s linear infinite;
  -ms-animation: move-twink-back 75s linear infinite;
  -o-animation: move-twink-back 75s linear infinite;
  -webkit-animation: move-twink-back 75s linear infinite;
  animation: move-twink-back 75s linear infinite;
}
